.course-box{
    height:15rem;
    display:flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    box-shadow: 5px 5px 8px rgba(0, 0, 0, 0.342);
    width:100%;
    padding:1rem;
    margin:2rem;
    margin-bottom:6rem;
    opacity:0;
    transition: all .5s;
}

.visible{
    opacity:1
}